@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&family=Source+Code+Pro:ital,wght@0,400;0,700;1,600&display=swap');

* {
    color: #fff;
    font-family: 'Roboto', sans-serif;
}

body {
    margin: 0;
    padding: 0;
}

.app {
   background-color: #343541;
   background-size: 100%;
    display: flex;
}
img {
    padding-right: 10px;
}

li {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}
li&:hover {
    background-color: #f0f0f0;
}

li&.active {
    background-color: #e0e0e0;
}

.Tab{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.side-bar {
  background-color: #202123;
  background-size: 100%;
  height: 100vh;
  width: 244px;
  display: flex;
  flex-direction: column;
    justify-content: space-between;

}

button {
    border: solid 0.5px rgba(255,255,255,0.5);
    background-color: transparent;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}

.main {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}

.main h1 {
    color: rgba(255,255,255,0.5);
}

nav {
    border-top:solid 0.5px rgba(255,255,255,0.5);
    padding: 10px;
    margin: 10px;

}

.Tabs {
 padding: 10px;
 margin: 10px;
 height: 100%;

}

.Tabs li {
    list-style-type: none;
    padding: 15px 0;
    cursor: pointer;
}
.info {
    color: rgba(255,255,255,0.5);
    font-size: 11px;
    padding: 10px;
}

.bottom-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input-container{
    position: relative;
    width: 100%;
    max-width: 650px;
}

input {
    width: 100%;
    border: none;
    font-size: 20px;
    background-color: rgba(255,255,255,0.05);
    padding: 12px 15px ;
    border-radius: 5px;
    box-shadow: rgba(0,0,0,0.05) 0 54px 55px,
     rgba(0,0,0,0.05) 0 -12px 30px,
     rgba(0,0,0,0.05) 0 4px 6px,
     rgba(0,0,0,0.05) 0 12px 3px,
     rgba(0,0,0,0.05) 0 -3px 5px;

}

input:focus {
    outline: none;
}

#submit {
    position: absolute;
    bottom: 15px;
    right: 0;
    cursor: pointer;
}

.feed {
    max-width: 650px;
    text-align: left;
    white-space: pre-line;

}
